@use 'base/variables' as v;
@use 'base/mixins' as m;

:root {
    --primario: #008037;
    --blanco: #FFF;
    --negro: #000000;
    --fuentePrincipal: 'Inter', sans-serif;
    --fuenteSecundaria: 'La Belle Aurore', cursive;
}

/* apply a natural box layout model to all elements, but allowing components to change */
html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*, *:before, *:after {
    box-sizing: inherit;
    padding: 0;
    margin: 0;
}

body {
    min-height: 100vh;
}

header .brand  {
    color: v.$blanco;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 2rem;
}

.section-main {
    position: relative;
    width: 100%;
    min-height: 100vh;
    background-image: url(/build/img/header-cel.jpg);
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem 8rem;
    background-attachment: fixed;
}

@include m.tablet { 
    .section-main{
        background-image: url(/build/img/headerpc.jpg);
    }
}

header {
    z-index: 999;
    position: fixed;
    top: 0; /* no es relevante */
    left: 0; /* no es relevante */
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 80px;
    transition: 0.6s;
}

header.down {
    background-color:#0a552b;
    padding: 15px 80px;
}

