    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: .2rem;
} */

header .menu {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

header .menu ul {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

header .menu ul li {
    list-style: none;
}

header .menu a {
    color: #fff;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 1.6rem;
    text-decoration: none;
    margin: 0 30px;
    border-radius: 2rem;
    transition: 0.3s;
    transition-property: color, background;
}

header .menu a:hover {
    color: #008037;
}

.section-two {
    padding: 2rem;
}

header .btn {
    color: #fff;
    font-size: 2.5rem;
    cursor: pointer;
    display: none;
}

@media (max-width:991px){ 
    header, header.down {
        padding: 20px 40px;
    }
    header .btn {
        display: block;
    }
    header .menu {
        position: fixed;
        background: #000000;
        min-height: 33px;
        height: 100vh;
        top: 0;
        right: -100%;
        padding: 80px 50px;
        transition-property: right;
    }
    header .menu ul {
        flex-direction: column;
    }
    header .menu.active {
        right: 0;
    }
    header .menu .close-btn {
        position: absolute;
        top: 0;
        left: 0;
        margin: 25px;
    }
    header .menu a {
        display: block;
        font-size: 20px;
        margin: 20px;
        padding: 0 15px;
    }
}

