@use 'base/mixins' as m;
@use 'base/variables' as v;

.inicio {
    background-image: url(/build/img/inicio.jpg);
    background-size: 100%;
    background-repeat: no-repeat;
    height: 100vh;
    margin: 0 auto;
    object-fit: cover;
    padding: 0;
    background-position: center;
}

@include m.tablet { 
    .inicio{
        background-image: url(/build/img/2.jpg);
    }
}