@use 'base/mixins' as m;
@use 'base/variables' as v;

.sucursal{
    background-image: url(/build/img/SUCURSALES.jpg);
    height: 100%;
    width: 100%;
    object-fit: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin: 0 auto;
    height: 5rem;
    margin-bottom: 2rem;
    margin-top: 2rem;
}

@include m.tablet { 
    .sucursal{
        display: none;
    }
}

.salta {
    background-image: url(/build/img/1.jpg);
    height: 85vh;
    width: 90%;
    object-fit: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin: 0 auto;
}

@include m.tablet { 
    .salta{
        background-image: url(/build/img/5.jpg);
        height: 100vh;
        width: 80%;
        background-size: 90%;
    }
}

.tucuman{
    background-image: url(/build/img/3.jpg);
    height: 85vh;
    width: 90%;
    object-fit: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin: 0 auto;
}

@include m.tablet { 
    .tucuman{
        background-image: url(/build/img/4.jpg);
        height: 100vh;
        width: 85%;
        background-size: 90%;
    }
}

.maps {
    margin: 0 auto;
}

.maps iframe {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

@include m.tablet { 
    .sucursal__grid{
        display: grid;
        grid-template-columns: repeat(2,1fr);
    }
}