@use 'base/mixins' as m;
@use 'base/variables' as v;

.negro{
    background-color:  #008037;
    color: #ffffff;
}

.okm__titulo{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 4rem;
}

@include m.tablet { 
    .okm__titulo{
        font-size: 6rem;
    }
}

.okm__parrafo {
    color: #ffffff;
    font-weight: 400;
    margin: 1rem 1rem -0.2rem 1rem;
}

@include m.tablet { 
    .okm__parrafo{
        margin-left: 10rem;
        font-size: 3rem;
    }
}

.okm__secciones{
    text-transform: uppercase;
    background-color: #ffffff;
    color: #008037;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 4rem 6rem;
    height: 6rem;
    font-weight: 900;
}

@include m.tablet { 
    .okm__secciones{
        height: 8rem;
        font-size: 3rem;
    }
}

.center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.okm__cuota {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    height: 15rem;
    margin: 2rem 10rem;
    flex-direction: column;
    font-size: 2.5rem;
    font-weight: 900;
    background-color: #ffffff;
    color: #008037;
}

@include m.tablet { 
    .okm__cuota {
        margin: 2rem 75rem;
    }
}


.okm__cuota span {
    font-size: 4rem;
    font-weight: 900;
}

@include m.tablet { 
    .okm__grid{
        display: grid;
        grid-template-columns: repeat(3,1fr);
    }
}

@include m.tablet { 
    .okm{
        display:none;
    }
    .okm__tablet{
        background-image: url(/build/img/okm__tablet.png);
        height: 90vh;
        width: 100%;
        background-color: #008037;
        background-repeat: no-repeat;
        background-position: center;
        background-size: 80%;
    }
}