@use 'base/variables' as v;
@use 'base/mixins' as m;

.clientes__grid {
    margin: 3rem;
    object-fit: cover;
}

@include m.tablet { 
    .clientes__grid{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 3rem;
    }
}

.clientes__img{
    margin-bottom: 2rem;
}