@use 'base/mixins' as m;
@use 'base/variables' as v;

.tienda{
    background-image: url(/build/img/tienda.jpg);
    height: 5rem;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    margin-bottom: 5rem;
    margin-top: 5rem;
}

@include m.tablet { 
    .tienda{
        height: 15rem;
        margin: 0 25rem;
        margin-top: 10rem;
        margin-bottom: 5rem;
        background-size: 60%
    }
}