@use 'base/mixins' as m;
@use 'base/variables' as v;

.titulo{
    text-align: center;
    font-size: 3rem;
    text-transform: uppercase;
    font-weight: 900;
    margin: 5rem 0;
    display: flex !important;
    justify-content: center;
    align-items: center;
}

@include m.tablet { 
    .titulo{
        font-size: 6rem;
    }
}

.subtitulo{
    font-size: 1.8rem;
}

@include m.tablet { 
    .subtitulo{
        font-size: 2rem;
    }
}

.seccion__motoplan {
    background-color:  #008037;
    color: #ffffff;
    border-radius: 100%;
    height: 16rem;
    margin: 1rem 7rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

@include m.tablet { 
    .seccion__motoplan {
        margin: 1rem 10rem;
    }
}

.span__motoplan {
    font-size: 3rem;
    display: block;
    font-weight: 900;
}

// grid 

@include m.tablet { 
    .motoplan__grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }
}