/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

:root {
  --primario: #008037;
  --blanco: #FFF;
  --negro: #000000;
  --fuentePrincipal: "Inter", sans-serif;
  --fuenteSecundaria: "La Belle Aurore", cursive;
}

/* apply a natural box layout model to all elements, but allowing components to change */
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  min-height: 100vh;
}

header .brand {
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 2rem;
}

.section-main {
  position: relative;
  width: 100%;
  min-height: 100vh;
  background-image: url(/build/img/header-cel.jpg);
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 8rem;
  background-attachment: fixed;
}

@media (min-width: 768px) {
  .section-main {
    background-image: url(/build/img/headerpc.jpg);
  }
}
header {
  z-index: 999;
  position: fixed;
  top: 0;
  /* no es relevante */
  left: 0;
  /* no es relevante */
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 80px;
  transition: 0.6s;
}

header.down {
  background-color: #0a552b;
  padding: 15px 80px;
}

/* header .brand {
    color: #fff;
    font-size: 3rem;
    font-weight: 700;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: .2rem;
} */
header .menu {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

header .menu ul {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

header .menu ul li {
  list-style: none;
}

header .menu a {
  color: #fff;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 1.6rem;
  text-decoration: none;
  margin: 0 30px;
  border-radius: 2rem;
  transition: 0.3s;
  transition-property: color, background;
}

header .menu a:hover {
  color: #008037;
}

.section-two {
  padding: 2rem;
}

header .btn {
  color: #fff;
  font-size: 2.5rem;
  cursor: pointer;
  display: none;
}

@media (max-width: 991px) {
  header, header.down {
    padding: 20px 40px;
  }

  header .btn {
    display: block;
  }

  header .menu {
    position: fixed;
    background: #000000;
    min-height: 33px;
    height: 100vh;
    top: 0;
    right: -100%;
    padding: 80px 50px;
    transition-property: right;
  }

  header .menu ul {
    flex-direction: column;
  }

  header .menu.active {
    right: 0;
  }

  header .menu .close-btn {
    position: absolute;
    top: 0;
    left: 0;
    margin: 25px;
  }

  header .menu a {
    display: block;
    font-size: 20px;
    margin: 20px;
    padding: 0 15px;
  }
}
