@use 'base/mixins' as m;
@use 'base/variables' as v;

.footer{
    background-color: #0a552b;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
}

.footer a {
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer h2{
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer h2:first-child {
    margin-top: 3rem;
}

.footer a:last-child{
    margin-bottom: 3rem;
}

@include m.tablet { 
    .footer__grid{
        display: grid;
        grid-template-columns: repeat(3,1fr);
    }
    .footer h2:first-child {
        margin-top: 6rem;
    }
}